}

.contact__options {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 0% 5%;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact-icon {
    font-size: 2rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 1rem;
}

/* Medium Devices */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        width: 65%;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* Small Devices */
@media screen and (max-width: 600px) {
    .contact__options {
        grid-template-columns: 1fr;
    }

    .contact__option {
        width: 100%;
        padding: 1.2rem 0rem;
    }
}
footer {
    margin-top: 2rem;
    background: black;
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    /* margin-top: -30rem; */
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.link {
    list-style-type: none;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: white;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer__copyright {
    margin-bottom: 4rem;
}


/* Small Devices */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}
.portfolio_intro_text {
    margin-left: 5%;
    margin-bottom: 2%;
}

.portfolio_intro_text::after {
    content: " ";
    position: absolute;
    border-top: 10px solid white;
    width: 50%;
    height: 0.2rem;
}

.portfolio__container {
    display: grid;
    margin-left: 5%;
    margin-right: 5%;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item {
    display: grid;
    background: var(--color-bg-variant);
    padding: 1.3rem;
    max-height: 27rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 0.5rem 0 0.5rem;
}

.portfolio__item-cta {
    display: flex;
    align-items: flex-end;
    grid-gap: 1rem;
    gap: 1rem;
}

.title_and_languages {
    display: grid;
    align-items: flex-end;
    grid-template-rows: auto;
}

.icon_under {
    background: transparent;
    display: flex;
    color: var(--color-bg-primary);
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    align-items: flex-end;
}

.icon_under :hover {
    color: white;
}

.site-links {
    font-size: 1rem;
    line-height: 1.5;
}

/* For Pop Up Window */
.pop_up_window {
    background: var(--color-bg-variant);
    border-radius: 1rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: white;
}

.pop_up_window>.pop_up_header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 10px 10px 10px;
}

.pop_up_window>.pop_up_content {
    width: 100%;
    padding: 10px 10px 10px;
}

.pop_up_actions {
    width: 100%;
    padding: 10px 5px;
    text-align: center;
    justify-content: center;
}


/* For Video Player */
.pop_up_window_video {
    background: var(--color-bg-variant);
    border-radius: 2rem;
    /* width: 100%; */
    /* width: fit-content;
    height: auto; */
    border-style: solid;
    border-width: 0.2rem;
    border-color: white;
}

.pop_up_content_video {
    /* width: 100%; */
    padding: 1rem 1rem 1rem;
}

.video__container>.video {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
}

.player-wrapper {
    width: 640px;
    height: auto;
}

.react-player {
    position: absolute;
    top: 0;
    left: 0;
}

