}

.portfolio__container_old {
    margin-top: 2rem;
    margin-bottom: 2rem;
    display: grid;
    /* margin-left: 5%; */
    margin-right: 5%;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
    font-size: 1.5rem;
}

.old_button {
    font-weight: 400;
