        gap: 1rem;
        grid-row-gap: 2rem;
        row-gap: 2rem;
    }

    .portfolio__item:hover {
        border-color: transparent;
        background: var(--color-bg-variant);
    }

    .portfolio__container_old {
        margin-top: 3rem;
        margin-bottom: 3rem;
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
        grid-row-gap: 2rem;
        row-gap: 2rem;
        overflow: scroll;
    }

    .portfolio__container_old > .portfolio__item {
        overflow: hidden;
    }

    .portfolio__item {
        display: grid;
        background: var(--color-bg-variant);
        padding-top: 2rem;
        padding-bottom: 2rem;
        max-height: 27rem;
        border-radius: 2rem;
        border: 0px solid transparent;
        transition: var(--transition);
    }

    .portfolio__item-cta {
        grid-gap: 0.1rem;
        gap: 0.1rem;
    }

    .pop_up_window {
        width: 80%;
        margin-left: 10%;
    }

    .player-wrapper {
        width: 300px;
    }

    .portfolio__item-image {
        border-radius: 1.5rem;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
}
header {
    height: 100vh;
    padding-top: 1rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* Links */
.links {
    margin-top: 1.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* Socials */
