    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

.socials::before {
    content: '';
    width: 2rem;
    height: 1px;
    background: var(--color-bg-primary);
}

.socials::after {
    content: '';
    width: 2rem;
    height: 1px;
    background: var(--color-bg-primary);
}

.me {
    width: 28rem;
    position: absolute;
    left: calc(50% - 14rem);
}

.particles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Medium Devices */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

